import { MsafeTransactionSubType } from '@/types'
import usePeripherySDKStore from '@cetus/stores/src/usePeripherySDKStore'

type HarvestParams = {
  pool_id: string
  position_nft_id: string
}

export default function useFarms() {
  const { peripherySdk } = usePeripherySDKStore()

  /**
   * Farms Stake Transaction Payload
   * @param clmmPosId
   * @param poolId
   * @returns
   */
  const getStakeTxPayload = async (clmmPosId: string, poolId: string) => {
    const parameter = {
      pool_id: poolId,
      clmm_position_id: clmmPosId
    }
    const tx = await peripherySdk.Farms.depositPayload(parameter)
    const msafeParams = {
      action: MsafeTransactionSubType.FarmingStake,
      txbParams: parameter
    }

    return {
      tx,
      msafeParams
    }
  }

  /**
   * Farms Unstake Transaction Payload
   * @param poolId
   * @param positionNftId 传posBaseInfo里的id就行
   * @returns
   */
  const getUnstakeTxPayload = async (poolId: string, positionNftId: string) => {
    const parameter = {
      pool_id: poolId,
      position_nft_id: positionNftId
    }

    const tx = await peripherySdk.Farms.withdrawPayload(parameter)

    const msafeParams = {
      action: MsafeTransactionSubType.FarmingUnstake,
      txbParams: parameter
    }
    return {
      tx,
      msafeParams
    }
  }

  /**
   * Farms claim rewards
   * @param poolId
   * @param positionNftId
   * @returns
   */
  const getHarvestFarmsTxPayload = async (poolId: string, positionNftId: string) => {
    const parameter = {
      pool_id: poolId,
      position_nft_id: positionNftId
    }
    const tx = await peripherySdk.Farms.harvestPayload(parameter)

    const msafeParams = {
      action: MsafeTransactionSubType.FarmingHarvest,
      txbParams: parameter
    }
    return {
      tx,
      msafeParams
    }
  }

  /**
   * Batch Harvest Farms rewards
   * @param params
   * @returns
   */
  const getBatchHarvestFarmsTxPayload = async (params: HarvestParams[]) => {
    const tx = await peripherySdk.Farms.batchHarvestPayload(params)
    const msafeParams = {
      action: 'FarmingBatchHarvest',
      txbParams: params
    }
    return {
      tx,
      msafeParams
    }
  }

  return {
    getStakeTxPayload,
    getUnstakeTxPayload,
    getHarvestFarmsTxPayload,
    getBatchHarvestFarmsTxPayload
  }
}
